import './App.css';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import SwiperCore, { Navigation, Pagination, A11y, Autoplay, EffectFade } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import axios from 'axios';
// import './swiper/swiper.scss';

function App() {
  const Link_Style = {color: 'white', textDecoration: 'none', fontWeight: 'bold'};
  // const pagelist = ['Home', 'Products', 'About Us'];
  return (
    <Router>
    <div className="nav_bar">
      <nav>
        <h1>Art_1</h1>
        <ul className="nav_item">
          <li>
            <Link style={Link_Style} to="/">Home</Link>
          </li>
          <li>
            <Link style={Link_Style} to="/products">Locations</Link>
          </li>
          <li>
            <Link style={Link_Style} to="/aboutus">About Us</Link>
          </li>
        </ul>
      </nav>
      <Switch>
        <Route path="/products">
          <Products />
        </Route>
        <Route path="/aboutus">
          <AboutUs />
        </Route>
        <Route path="/" exact>
          <Home />
        </Route>
      </Switch>
    </div>
    </Router>
  );
}

